.slick-arrow {
  background-color: rgba(#090909, 0.6);
  transition: all 0.2s;
  width: 60px;
  height: 60px;
  z-index: 2;
  line-height: 60px;
  @media (max-width: 768px) {
    display: none !important; }

  .icon {
    font-size: 20px;
    color: white; }
  &:hover {
    background-color: #090909;
    width: 120px;
    .slide-count {
      .slide-current {
        opacity: 1; }
      .line {
        transform: scale(1, 1) rotate(45deg);
        opacity: 0.4; }
      .slide-total {
        opacity: 1; } } }
  &.slick-prev {
    left: 0;
    text-align: left;
    .icon {
      margin-left: 22px; }
    .slide-count {
      right: 0; } }
  &.slick-next {
    right: 0;
    text-align: right;
    .icon {
      margin-right: 22px; }
    .slide-count {
      left: 0; } } }

.slide-count {
  position: absolute;
  width: 60px;
  height: 60px;
  top: 0;
  .slide-current {
    opacity: 0;
    position: absolute;
    top: 16px;
    left: 16px;
    height: 10px;
    line-height: 10px;
    font-size: 12px;
    font-family: $Helvetica;
    font-weight: 300;
    color: white; }
  .line {
    transform: scale(0, 0) rotate(45deg);
    opacity: 0;
    width: 1px;
    height: 25px;
    background-color: white;
    position: absolute;
    top: 17px;
    left: 28px; }
  .slide-total {
    opacity: 0;
    position: absolute;
    top: 35px;
    left: 36px;
    height: 10px;
    line-height: 10px;
    font-size: 12px;
    font-family: $Helvetica;
    font-weight: 300;
    color: white; } }

