.network__map, #map__network {
  width: 100%;
  height: 500px;
  position: relative;

  .marker-custom {
    position: relative;
    display: inline-block;
    width: 15px;
    height: 15px;
    border-radius: 100%;
    background-color: #bc9e52;
    transition: all 0.3s;

    &:after {
      content: '';
      width: 50px;
      height: 50px;
      display: inline-block;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      background-color: #bc9e52;
      opacity: 0.4;
      z-index: -1;
      border-radius: 100%;
      transition: all 0.6s;
      animation-name: pulse;
      animation-duration: 0.6s;
      animation-timing-function: linear;
      animation-iteration-count: infinite;
      animation-direction: alternate; } } }

@keyframes pulse {
  0% {
    width: 50px;
    height: 50px; }
  100% {
    width: 40px;
    height: 40px; } }
