// used in structure-sidebars.less, structure-grid.less and structure-grid-px.less
//-----------------------------------------------------------------------------------
$widthContent: 960px;
$widthPageMax: none;
$widthPageMin: 0;

html {
 }  //height: 100%

body {
  overflow-y: auto;
  overflow-x: hidden;
  .no-flexbox & {
    min-height: 100%;
    //height: 100%
    display: block !important; } }

.page-wrapper,
.footer {
  max-width: $widthPageMax;
  min-width: $widthPageMin;
  width: 100%;
  margin: 0 auto;
  .no-flexbox & {
    width: auto; } }

.page-wrapper {

  .lte-ie-11 &,
  .lt-ie-11 & {
    min-height: 0; }

  .no-flexbox & {
    display: block !important;
    width: auto;
    min-height: 100%;
    height: auto !important;
    height: 100%; } }

.page-body {
  margin-bottom: 155px;
  padding-top: 85px;
  position: relative;
  z-index: 2;
  background: white;
  @media (max-width: 992px) {
    padding-top: 55px; }
  @media (max-width: 768px) {
    margin-bottom: 265px; } }


.wrapper {
  position: relative; }

*, *:after, *:before {
  box-sizing: border-box; }
