// elements
//-----------------------------------------------------------------------------------
.bg-pattern {
  position: relative;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  &:after {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: url(../img/pattern.png) center; } }

.el-btn {
  display: inline-block;
  border: solid 2px #090909;
  padding: 0 15px;
  height: 50px;
  line-height: 48px;
  font-family: $Helvetica;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  color: #090909;
  min-width: 180px;
  transition: all 0.2s;
  .mob-hide {
    @media (max-width: $screen-xs-max) {
      display: none; } }
  &:hover {
    text-decoration: none;
    background: #BC9E52;
    color: white;
    border-color: #BC9E52; }
  &.m-150 {
    min-width: 150px; }

  &.mod-sm {
    height: 38px;
    line-height: 36px; }
  &.mod-white {
    color: white;
    border-color: white;
    &:hover {
      background: #BC9E52;
      color: white;
      border-color: #BC9E52; } } }


.el-link-sm {
  display: inline-block;
  position: relative;
  text-decoration: none;

  &:before {
    content: '';
    position: absolute;
    z-index: 0;
    bottom: -3px;
    left: 0;
    width: 100%;
    height: 1px;
    transform: scale3d(0,1,1);
    transform-origin: 100% 50%;
    transition: transform 0.5s;
    transition-timing-function: cubic-bezier(0.2,1,0.3,1);
    background: #bc9e52; }
  &:hover, &:focus {
    text-decoration: none; }
  &:hover:before,
  &:focus:before {
    transform: scale3d(1,1,1);
    transform-origin: 0 50%;
    transition-timing-function: ease; } }

.title-arrow {
  display: block;
  width: 100%;
  padding-left: 22px;
  position: relative;
  font-family: $Helvetica;
  font-size: 20px;
  font-weight: 300;
  line-height: 1.2;
  text-align: left;
  color: #bc9e52;
  margin-bottom: 15px;
  @media (max-width: 768px) {
    font-size: 16px; }

  &:before {
    content: '';
    display: inline-block;
    position: absolute;
    left: 0;
    top: 3px;
    width: 12px;
    height: 10px;
    background: url(../img/svg/arrow-color.svg) no-repeat center/contain;
    @media (max-width: 768px) {
      top: 1px; } } }

.el-list {
  display: inline-block;

  &.mod-white {
    li {
      color: #ffffff; } }
  &.mod-m {
    margin-bottom: 16px; }
  &.mod-xs {
    li {
      padding-left: 18px;
      font-size: 16px;
      line-height: 1.56;
      &:not(:last-child) {
        margin-bottom: 12px; }

      &:before {
        top: 6px;
        width: 9px;
        height: 7px;
        background: url(../img/svg/arrow-black.svg) no-repeat center/contain; } } }
  li {
    width: 100%;
    padding-left: 22px;
    position: relative;
    font-family: $Helvetica;
    font-size: 20px;
    font-weight: 300;
    line-height: 1;
    text-align: left;
    color: #090909;
    @media (max-width: 768px) {
      font-size: 16px; }
    &:not(:last-child) {
      margin-bottom: 20px; }
    &:before {
      content: '';
      display: inline-block;
      position: absolute;
      left: 0;
      top: 3px;
      width: 12px;
      height: 10px;
      background: url(../img/svg/arrow-white.svg) no-repeat center/contain; } } }


#miniTip {
  font-family: $Helvetica;
  font-size: 12px;
  line-height: 1.3;
  font-weight: normal;
  //border: 1px solid #bc9e52
  -webkit-box-shadow: 0 5px 15px 0 rgba(61, 110, 146, 0.1);
  box-shadow: 0 5px 15px 0 rgba(61, 110, 146, 0.1); }
