.remodal {
  padding-bottom: 45px;
  max-width: 580px;
  h2 {
    font-family: $Helvetica;
    font-size: 34px;
    font-weight: 300;
    color: #090909;
    line-height: 1.3;
    margin-bottom: 45px;
    @media (max-width: 768px) {
      font-size: 17px;
      margin-bottom: 20px; }
    b {
      display: block; } }

  .el-input {
    display: block;
    margin: 0 auto 50px;
    max-width: 440px;
    width: 100%;
    @media (max-width: 768px) {
      margin-bottom: 20px; }
    input {
      display: block;
      padding: 10px 13px 10px 13px;
      margin: 0 0 0 0;
      background: #FFF;
      border: 1px solid #e9e8e8;
      border-radius: 5px;
      width: 100%;
      height: 45px;
      font-family: $Helvetica;
      font-size: 15px;
      font-weight: normal;
      line-height: 18px;
      color: #231f20;
      box-shadow: 0 0 0 0 transparent; } } }
