// header
//-----------------------------------------------------------------------------------
.header-wrap {
  z-index: 99;
  transform: translateY(0);
  top: 0;
  width: 100%;
  position: absolute;
  @media (max-width: $screen-sm-max) {
    position: fixed; }
  &.detached {
    position: fixed;
    transition: transform .2s; }
  &.invisible {
    transform: translateY(-100%);
    pointer-events: none;
    @media (max-width: $screen-sm-max) {
      transform: translateY(0);
      pointer-events: all; } } }


.header {
  z-index: 999;
  width: 100%;
  background-color: #090909;
  padding: 18px 0;
  @media (max-width: $screen-sm-max) {
    padding: 10px 0; }
  &.openmenu {
    .menu {
      .icon-menu {
        display: none; }
      .icon-cancel {
        display: inline-block; } }

    .nav-wrap {
      transform: translateX(0px); } }
  .call {
    display: none;
    font-size: 32px;
    margin-top: 3px;
    margin-right: 30px;
    color: white;
    float: right;
    position: relative;
    z-index: 9;
    @media (max-width: $screen-sm-max) {
      display: inline-block; } }
  .menu {
    display: none;
    font-size: 24px;
    color: white;
    margin-top: 6px;
    float: right;
    position: relative;
    z-index: 9;
    @media (max-width: $screen-sm-max) {
      display: inline-block; }
    .icon-menu {
      display: inline-block; }
    .icon-cancel {
      font-size: 18px;
      display: none; } }
  .nav-wrap {
    @extend %clear-fix;
    display: inline-block;
    float: right;
    margin: 5px 0;
    @media (max-width: $screen-sm-max) {
      overflow-y: auto;
      overflow-x: hidden;
      position: fixed;
      right: 0;
      top: 0;
      width: 320px;
      background-color: #090909;
      height: 100vh;
      padding-top: 70px;
      padding-left: 30px;
      padding-right: 30px;
      transition: transform 0.2s;
      transform: translateX(320px);
      z-index: 4;
      min-height: 430px; } }


  .el-btn {
    float: left;
    @media (max-width: $screen-md-max) {
      .mob-hide {
        display: none; } }
    @media (max-width: $screen-sm-max) {
      float: none; } }

  .el-link-sm:before {
    bottom: 1px; }

  .phone {
    float: left;
    display: inline-block;
    font-family: $Helvetica;
    font-size: 16px;
    text-align: left;
    color: #ffffff;
    font-weight: 400;
    margin-left: 30px;
    line-height: 38px;
    &:hover {
      text-decoration: none; }

    @media (max-width: $screen-sm-max) {
      margin-left: 0;
      float: none;
      display: block; } }

  .logo {
    @extend %clear-fix;
    display: inline-block;
    float: left;
    .icon {
      float: left;
      display: inline-block;
      width: 92px;
      height: 49px;
      margin-right: 9px;
      @media (max-width: $screen-sm-max) {
        width: 70px;
        height: 38px; } }
    .label {
      margin-top: 9px;
      float: left;
      display: inline-block;
      width: 92px;
      height: 34px;
      @media (max-width: $screen-sm-max) {
        width: 65px;
        height: 25px; } } }

  .navigation {
    display: inline-block;
    margin-right: 30px;
    float: left;
    .drop-menu {
      position: absolute;
      left: 50%;
      margin-top: 26px;
      margin-left: -100px;
      top: 36px;
      background: #bc9e52;
      width: 280px;
      padding: 10px 15px;
      z-index: 9;
      transition: all 0.2s;
      opacity: 0;
      visibility: hidden;
      &:before {
        content: '';
        width: 100%;
        height: 26px;
        position: absolute;
        top: -26px;
        left: 0; }
      @media (max-width: $screen-sm-max) {
        background: none;
        position: static;
        opacity: 1;
        visibility: visible;
        display: block;
        margin-top: -8px;
        margin-left: 0; }
      a {
        color: white;
        line-height: 30px;
        font-family: $Helvetica;
        font-size: 16px;
        font-weight: normal; } }

    & > ul {
      @extend %clear-fix;
      & > li {
        position: relative;
        float: left;
        display: inline-block;
        @media (max-width: $screen-sm-max) {
          float: none;
          display: block; }
        &:not(:last-child) {
          margin-right: 40px; }
        &:hover {
          & > a .icon {
            transform: rotate(180deg); }

          .drop-menu {
            opacity: 1;
            visibility: visible;
            @media (max-width: $screen-sm-max) {
              display: block; } } }
        & > a {
          font-family: $Helvetica;
          font-size: 16px;
          font-weight: normal;
          text-align: right;
          color: #ffffff;
          display: inline-block;
          line-height: 38px;
          &:hover {
            text-decoration: none; }
          .icon {
            width: 1em;
            font-size: 10px;
            margin-left: 5px;
            @media (max-width: $screen-sm-max) {
              display: none; } } } } } } }
