$base64-png: 'image/png;base64';
$base64-jpg: 'image/jpeg;base64';
$base64-svg: 'image/svg+xml;base64';

// font var
//-----------------------------------------------------------------------------------
$default-font: Arial, 'Helvetica Neue', Helvetica, sans-serif;

@include font-face('Helvetica', '../fonts/HelveticaNeueCyr-Light', 'HelveticaNeueCyr-Light', 300);
@include font-face('Helvetica', '../fonts/HelveticaNeueCyr-Roman', 'HelveticaNeueCyr-Roman', normal);
@include font-face('Helvetica', '../fonts/HelveticaNeueCyr-Bold', 'HelveticaNeueCyr-Bold', bold);

$Helvetica: 'Helvetica', $default-font;

// selection color
//-----------------------------------------------------------------------------------
$color-selection: dodgerblue;

// color var
//-----------------------------------------------------------------------------------
$color-link: dodgerblue;
