// footer
//-----------------------------------------------------------------------------------
.footer {
  background-color: #090909;
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 1;
  will-change: opacity;
  padding: 45px 0;

  .cop {
    font-family: $Helvetica;
    font-weight: normal;
    font-size: 12px;
    text-align: left;
    color: rgba(255, 255, 255, 0.5);
    padding-left: 5px; }

  .polis {
    display: inline-block;
    color: white;
    font-family: $Helvetica;
    font-size: 12px;
    font-weight: normal;
    text-align: center;
    margin-right: 5px;
    @media (max-width: $screen-xs-max) {
      margin-top: 25px; } }

  .nav {
    width: 100%;
    text-align: center;
    margin-bottom: 22px;
    @media (max-width: $screen-xs-max) {
      text-align: left; }
    ul {
      display: inline-block;
      @extend %clear-fix;
      li {
        position: relative;
        float: left;
        padding: 0 95px;
        text-align: center;
        @media (max-width: $screen-md-max) {
          padding: 0 42px; }
        @media (max-width: $screen-sm-max) {
          padding: 0 15px; }
        @media (max-width: $screen-xs-max) {
          float: none;
          padding: 0 10px;
          text-align: left; }
        &:not(:last-child):after {
          content: '';
          display: inline-block;
          position: absolute;
          right: 0;
          width: 1px;
          height: 24px;
          top: 0;
          background: rgba(white, 0.2);
          @media (max-width: $screen-xs-max) {
            display: none; } }

        &:last-child {
          text-align: right;
          padding-right: 0;
          @media (max-width: $screen-xs-max) {
            text-align: left; } }
        &:first-child {
          text-align: left;
          padding-left: 0;
          @media (max-width: $screen-xs-max) {
            padding-left: 10px; } }
        a {
          display: inline-block;
          font-family: $Helvetica;
          font-weight: normal;
          font-size: 16px;
          line-height: 24px;
          color: #ffffff;
          @media (max-width: $screen-sm-max) {
            font-size: 14px; }
          &:hover {
            text-decoration: none; } } } } } }
